import moment from 'moment'
import _ from 'lodash'
import  MorphSVGPlugin  from "gsap/dist/MorphSVGPlugin"


import {Vue,Component} from 'vue-property-decorator'
import { ArcCurve } from 'three'
@Component({
  filters:{
    formatDate(timestamp: number){
      return moment(timestamp).format("MMM DD, YYYY HH:mm")
    }
  }
})
export default class Note extends Vue {
  private dark = false
  private isOpen = false
  private db: any
  private notes: any = []
  private colors: string[] = [
    'linear-gradient(45deg, rgb(230, 29, 140), rgb(199, 233, 251))',
    'linear-gradient(45deg, rgb(220, 248, 239), rgb(254, 226, 248))',
    'linear-gradient(45deg, rgb(171, 220, 255), rgb(3, 150, 255))',
    'linear-gradient(45deg, rgb(254, 182, 146), rgb(234, 84, 85))',
    'linear-gradient(45deg, rgb(255, 246, 183), rgb(246, 65, 108))',
    'linear-gradient(45deg, rgb(253, 235, 113), rgb(248, 216, 0))',
    'linear-gradient(45deg, rgb(255, 247, 32), rgb(60, 213, 0))',
    'linear-gradient(45deg, rgb(112, 245, 112), rgb(73, 198, 40))',
    'linear-gradient(45deg, rgb(105, 255, 151), rgb(0, 228, 255))',
    'linear-gradient(45deg, rgb(60, 140, 231), rgb(0, 234, 255))',
    'linear-gradient(45deg, rgb(97, 37, 112), rgb(30, 175, 237))',
    'linear-gradient(45deg, rgb(250, 178, 255), rgb(25, 4, 229))'
  ]
  private mounted(){
    let that = this
    gsap.registerPlugin(MorphSVGPlugin);
    // init IndexDB
    const request = indexedDB.open('nebulas',1)
    request.onsuccess = function (event) {
      that.db = request.result
      that.getNote()
    }
    request.onupgradeneeded = function (event) {
      if (!(<IDBDatabase>request.result).objectStoreNames.contains('note')) {
        (<IDBDatabase>request.result).createObjectStore('note',{keyPath: 'timestamp'})
      }
    }
  }
  private toggle(){
    this.dark = !this.dark
    if(this.dark){
      this.toDark()
    }else{
      this.toLight()
    }
    gsap.to("#sun",
    {
      duration:1,
      morphSVG:{
        shape: this.dark ? '#moon' : '#sun'
      }
    })
  }
  private toDark(){
    // const r = this.getRadius(window.innerHeight / 2, window.innerWidth / 2)
    // gsap.to('#c1',{width: 2*r,height:2*r, top: window.innerHeight / 2 - r,left: window.innerWidth / 2 - r,duration: 1.5,opacity: 1,ease: 'power1.out'})
    // gsap.to('#c2',{width: 4*r/3,height: 4*r/3,top: window.innerHeight / 2 - 2*r/3,left: window.innerWidth / 2 - 2*r/3, duration: 1,opacity: 1,ease: 'power1.out'})
    // gsap.to('#c3',{width: 2*r/3,height:2*r/3,top: window.innerHeight / 2 - r/3,left: window.innerWidth / 2 - r/3, duration: 0.5,opacity: 1,ease: 'power1.out'})
    gsap.fromTo('#bg',{width: 0,   left: 0},{left: 0,width: window.innerWidth, duration: 1, delay: 0,opacity: 1,ease: 'none'})
  }
  private toLight(){
    gsap.fromTo('#bg',{width: window.innerWidth,left: 0},{left: window.innerWidth, width: 0, duration: 1, delay: 0,opacity: 1,ease: 'none'})
    // const r = 0
    // gsap.to('#c1',{width: 2*r,height:2*r, top: window.innerHeight / 2 - r,left: window.innerWidth / 2 - r,duration: 0.5, delay: 0,opacity: 1,ease: 'power1.out'})
    // gsap.to('#c2',{width: 4*r/3,height: 4*r/3,top: window.innerHeight / 2 - 2*r/3,left: window.innerWidth / 2 - 2*r/3, duration: 0.5, delay: 0.1,opacity: 1,ease: 'power1.out'})
    // gsap.to('#c3',{width: 2*r/3,height:2*r/3,top: window.innerHeight / 2 - r/3,left: window.innerWidth / 2 - r/3, duration: 0.5, delay: 0.2,opacity: 1,ease: 'power1.out'})
  }
  private getRadius(x: number,y: number){
    return Math.sqrt(Math.pow(x,2)+Math.pow(y,2))
  }
  private open(){
    this.isOpen = !this.isOpen
    if(this.isOpen){
      gsap.to('#add', {rotate: 360,duration: 0.5})
      gsap.to('.color',{width:25,height:25,duration:1, ease: "elastic.out(1, 0.5)"})
    }else{
      gsap.to('#add', {rotate: -360,duration: 0.5})
      gsap.to('.color',{width:0,height:0,duration:1, ease: "elastic.out(1, 0.3)"})
    }
    
  }
  private reverse(arr: any[]){
    return arr.reverse()
  }
  private addNew($event: MouseEvent, index: number){
    let note = {
      bg: this.colors[index],
      expand: false,
      editing: true,
      timestamp: moment().valueOf(),
      content: ""
    }
    this.notes.unshift(note)
    // let notes = document.querySelector(`#content`)
    // let next = this.newEle(this.notes.length - 1);
    // if(this.notes.length == 1) 
    // {
    //   (notes as HTMLElement).appendChild(next)
    // }
    // else{
    //   let before = document.querySelector(`#note${this.notes.length - 2}`);
    //  (notes as HTMLElement).insertBefore(next, before)
    // }
    setTimeout(()=>{
      const r = 100
      gsap.to(`#note${this.notes[0].timestamp}>.bg`, {width: 2*r, height: 2*r, top: 100 - r, left: 100 - r,borderRadius: 10, duration: 2,ease: "elastic.out(1, 0.3)"})
      gsap.to(`#note${this.notes[0].timestamp}>.shadow`, {width: 2*r, height: 2*r, top: 100 - r, left: 100 - r,borderRadius: 10, duration: 2,ease: "elastic.out(1, 0.3)"})
      gsap.to(`#note${this.notes[0].timestamp}>.inner`, {opacity: 1,duration: 1, delay: 1})
    }, 100)
  }
  private newEle(index: number){
    let note = document.createElement('div')
    note.setAttribute('id',`note${index}`)
    note.setAttribute('class',`note`)
    note.style.position = ""
    let bg = document.createElement('div')
    bg.setAttribute('class',`bg`)
    bg.style.background = this.notes[index].bg
    bg.style.borderRadius = '10px'
    bg.style.position = "absolute"
    bg.style.top = "100px"
    bg.style.left = "100px"
    note.appendChild(bg)
    return note
  }
  private expand($event: MouseEvent,index: number){
    let that = this
    this.notes[index].expand = !this.notes[index].expand
    if ( this.notes[index].expand) {
      gsap.to(`#note${that.notes[index].timestamp} #start1`,{morphSVG:{shape:`#note${that.notes[index].timestamp} #end1`}})
      gsap.to(`#note${that.notes[index].timestamp} #start2`,{morphSVG:{shape:`#note${that.notes[index].timestamp} #end2`}})

      //show item
      gsap.to(`#note${that.notes[index].timestamp} .edit`,{translateY: 40,translateX: -5,ease: "elastic.out(1, 0.3)",duration: 1.3})
      gsap.to(`#note${that.notes[index].timestamp} .star`,{translateX: Math.sqrt(2) * 20, translateY: Math.sqrt(2) * 20,ease: "elastic.out(1, 0.3)",duration: 1.3})
      gsap.to(`#note${that.notes[index].timestamp} .delete`,{translateX: 40, translateY: -5,ease: "elastic.out(1, 0.3)",duration: 1.3})
      
    } else {
      gsap.to(`#note${that.notes[index].timestamp} #start1`,{morphSVG:{shape:`#note${that.notes[index].timestamp} #start1`}})
      gsap.to(`#note${that.notes[index].timestamp} #start2`,{morphSVG:{shape:`#note${that.notes[index].timestamp} #start2`}})
      //show item
      gsap.to(`#note${that.notes[index].timestamp} .edit`,{translateX: 0,translateY: 0})
      gsap.to(`#note${that.notes[index].timestamp} .star`,{translateX: 0, translateY: 0})
      gsap.to(`#note${that.notes[index].timestamp} .delete`,{translateX: 0, translateY: 0})
    }
    
  }
  private putNote($event: MouseEvent,index: number){
    let that = this
    $event.stopPropagation()
    if(!that.notes[index].editing){
      that.notes[index].editing = true
      gsap.to(`#note${that.notes[index].timestamp} #start1`,{morphSVG:{shape:`#note${that.notes[index].timestamp} #start1`}})
      gsap.to(`#note${that.notes[index].timestamp} #start2`,{morphSVG:{shape:`#note${that.notes[index].timestamp} #start2`}})
      //show item
      gsap.to(`#note${that.notes[index].timestamp} .edit`,{translateX: 0,translateY: 0})
      gsap.to(`#note${that.notes[index].timestamp} .star`,{translateX: 0, translateY: 0})
      gsap.to(`#note${that.notes[index].timestamp} .delete`,{translateX: 0, translateY: 0})
      return
    }
    that.notes[index].expand = false
    that.notes[index].editing = false 
    const request = (that.db as IDBDatabase).transaction('note', 'readwrite').objectStore('note').put(that.notes[index])

    request.onsuccess = function (event) {
      gsap.to(`#note${that.notes[index].timestamp} #start1`,{morphSVG:{shape:`#note${that.notes[index].timestamp} #start1`}})
      gsap.to(`#note${that.notes[index].timestamp} #start2`,{morphSVG:{shape:`#note${that.notes[index].timestamp} #start2`}})
      //show item
      gsap.to(`#note${that.notes[index].timestamp} .edit`,{translateX: 0,translateY: 0})
      gsap.to(`#note${that.notes[index].timestamp} .star`,{translateX: 0, translateY: 0})
      gsap.to(`#note${that.notes[index].timestamp} .delete`,{translateX: 0, translateY: 0})
      console.log('数据更新成功');
    };

    request.onerror = function (event) {
      console.log('数据更新失败');
    }
    
  }
  private getNote(){
    let that = this
    const objectStore = (that.db as IDBDatabase).transaction('note', 'readwrite').objectStore('note')
    objectStore.openCursor().onsuccess = function(event: any){
      let cursor = event.target.result
      if (cursor) {
        that.notes.push({
          bg: cursor.value.bg,
          editing: cursor.value.editing,
          expand: cursor.value.expand,
          content: cursor.value.content,
          timestamp: cursor.value.timestamp
        })
        cursor.continue()
      }
      else{
        let arr = [...that.notes]
        that.notes = _.orderBy(arr, ['timestamp'], ['desc'])
        that.notes.forEach((item: any, index: number) => {
          const r = 100
          gsap.to(`#note${that.notes[index].timestamp}>.bg`, {width: 2*r, height: 2*r, top: 100 - r, left: 100 - r,borderRadius: 10, duration: 2,ease: "elastic.out(1, 0.3)"})
          gsap.to(`#note${that.notes[index].timestamp}>.shadow`, {width: 2*r, height: 2*r, top: 100 - r, left: 100 - r,borderRadius: 10, duration: 2,ease: "elastic.out(1, 0.3)"})
          gsap.to(`#note${that.notes[index].timestamp}>.inner`, {opacity: 1,duration: 1, delay: 1})
        });
      }
    }
  }
  private deleteNote($event: MouseEvent,index: number){
    let that = this
    $event.stopPropagation()
    const request = (that.db as IDBDatabase).transaction('note', 'readwrite')
    .objectStore('note')
    .delete(that.notes[index].timestamp)

    request.onsuccess = function(e) {
      
      gsap.to(`#note${that.notes[index].timestamp} #start1`,{morphSVG:{shape:`#note${that.notes[index].timestamp} #start1`}})
      gsap.to(`#note${that.notes[index].timestamp} #start2`,{morphSVG:{shape:`#note${that.notes[index].timestamp} #start2`}})
      //show item
      gsap.to(`#note${that.notes[index].timestamp} .edit`,{translateX: 0,translateY: 0})
      gsap.to(`#note${that.notes[index].timestamp} .star`,{translateX: 0, translateY: 0})
      gsap.to(`#note${that.notes[index].timestamp} .delete`,{translateX: 0, translateY: 0})

      // const r = 0
      // gsap.to(`#note${that.notes[index].timestamp}>.inner`, {opacity: 0,duration: 0.5})
      // gsap.to(`#note${that.notes[index].timestamp}>.bg`, {width: 2*r, height: 2*r, top: 100 - r, left: 100 - r,borderRadius: 10, duration: 2,ease: "elastic.out(1, 0.3)"})
      // gsap.to(`#note${that.notes[index].timestamp}>.shadow`, {width: 2*r, height: 2*r, top: 100 - r, left: 100 - r,borderRadius: 10, duration: 2,ease: "elastic.out(1, 0.3)"})

      setTimeout(()=>{
        let arr = [...that.notes]
        _.pullAt(arr, index)
        that.notes = arr
      },1000)
    }
  }
  private star(){
    let that = this
    console.log(that)
  }
}
