
























































































































































































































































































































import moment from 'moment-timezone'
import city from './heritage_data'
import gsap from 'gsap'
import echarts from 'echarts'
import axios,{AxiosResponse} from 'axios'
import {Vue,Component} from 'vue-property-decorator'
import { ClampToEdgeWrapping } from 'three'

interface Onecall {
  lat?: number;
  lon?: number;
  timezon?: string;
  timezone_offset?: number;
  current?: Object;
  hourly?: any[];
  daily?: any[]
}


@Component({
  filters:{
    format(value: number,tz: string,str: string){
      if(!value) return ''
      return moment.tz(value*1000, tz).format(str)
    }
  }
})
export default class Phone extends Vue{
  private location: any = city[0]
  private icon = 'https://openweathermap.org/img/wn/10d@2x.png'
  private top = true
  private hour = true
  private menu  = false
  private hourChart: any
  private dayChart: any
  private onecall: Onecall = {
    hourly: [],
    daily: []
  }
  private hourLine :number[][] =[]
  private dayLine: number[][]=[]
  private list: any[]=[]
  private interval: any
  private prevent($event: MouseWheelEvent){
    $event.stopPropagation()
  }
  private scroll($event: MouseWheelEvent){
    if($event.deltaY > 0){
      if(this.top){
        gsap.to('.main',{translateY: '-100%',duration: 1})
        this.top = false
      }
    }else{
      if(!this.top){
        gsap.to('.main',{translateY: '0%',duration: 1})
        this.top = true
      }
    }
  }
  private mounted(){
    
    this.getWeather(Number(this.location.latitude), Number(this.location.longitude) )
    this.getList()
    //this.getWeather(Number(31.24), Number(120.69) )
    this.interval = setInterval(()=>{
      this.getWeather(Number(this.location.latitude), Number(this.location.longitude) )
    },60000)
  }
  private beforeDestroy(){
    clearInterval(this.interval)
  }
  private toggle(value: boolean){
    this.hour = value
    if(value){
      this.getHourLine(this.hourLine)
      this.dayChart.dispose()
    }else{
      this.getDayLine(this.dayLine)
      this.hourChart.dispose()
    }
  }
  private getDayLine(data: number[][]){
    this.dayChart = echarts.init((document.querySelector('#days-line') as HTMLDivElement))
    const option = {
          grid: {
            bottom: 18,
            right: 40
          },
          legend: {
              data: ['MAX', 'MIN'],
              top: -5
          },
          xAxis: {
              type: 'category',
              boundaryGap: false
          },
          yAxis: {
              type: 'value',
              axisLabel: {
                  formatter: '{value}'
              }
          },
          series: [
              {
                  name: 'MAX',
                  type: 'line',
                  areaStyle: {},
                  data: data[0],
                  markPoint: {
                      data: [
                          {type: 'max', name: 'max'},
                          {type: 'min', name: 'min'}
                      ]
                  },
                  markLine: {
                      data: [
                          {type: 'average', name: '平均值'}
                      ]
                  }
              },
              {
                  name: 'MIN',
                  type: 'line',
                  data: data[1],
                  areaStyle: {},
                  markPoint: {
                      data: [
                          {name: '周最低', value: -2, xAxis: 1, yAxis: -1.5}
                      ]
                  },
                  markLine: {
                      data: [
                          {type: 'average', name: '平均值'},
                          [{
                              symbol: 'none',
                              x: '90%',
                              yAxis: 'max'
                          }, {
                              symbol: 'circle',
                              label: {
                                  position: 'start',
                                  formatter: 'max'
                              },
                              type: 'max',
                              name: 'max'
                          }]
                      ]
                  }
              }
          ]
    };
    this.dayChart.setOption(option)
  }
  private getHourLine(data: number[][]){
    this.hourChart = echarts.init((document.querySelector('#line') as HTMLDivElement))
    const option = {
          grid: {
            bottom: 0,
            right: 0,
            left: 0
          },
          legend: {
              data: ['Feels Like']
          },
          xAxis: {
              type: 'category',
              boundaryGap: false,
              show: false
          },
          yAxis: {
              type: 'value',
              axisLabel: {
                  formatter: '{value}'
              },
              show: false
          },
          series: [
              {
                  name: 'Feels Like',
                  type: 'line',
                  data: data[1],
                  label:{
                    show: false,
                    color: '#000'
                  },
                  markPoint: {
                      data: [
                          {type: 'max', name: '最大值'},
                          {type: 'min', name: '最小值'}
                      ]
                  },
                  areaStyle: {
                    color: {
                        type: 'linear',
                        x: 0,
                        y: 0,
                        x2: 1,
                        y2: 0,
                        colorStops: [{
                            offset: 0, color: 'red' // 0% 处的颜色
                        }, {
                            offset: 1, color: 'blue' // 100% 处的颜色
                        }],
                        global: false // 缺省为 false
                    }
                  },
                  itemStyle: {
                    opacity: 0
                  },

              }
          ]
    };
    this.hourChart.setOption(option)
  }
  private getWeather(lat: number, lon: number){
    const URL = `https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lon}&lang=en& exclude=hourly,daily&units=metric&appid=207c24b8a2b7b48ac770562514f3a809`
    axios.get(URL).then((res: AxiosResponse)=>{
      if(res.status==200){
        this.onecall = res.data
        this.hourLine[0] = (this.onecall.hourly || []).map(item => parseInt(item.temp)) 
        this.hourLine[1] = (this.onecall.hourly || []).map(item => parseInt(item.feels_like)) 
        this.dayLine[0] = (this.onecall.daily || []).map(item => parseInt(item.temp.max)) 
        this.dayLine[1] = (this.onecall.daily || []).map(item => parseInt(item.temp.min))
        this.getHourLine(this.hourLine)
        this.getDayLine(this.dayLine)
      }
    })
  }
  private getList(){
    let list: any[] = []
    for(let i =0;i<city.length;i++){
      let item = city[i]
      const URL = `https://api.openweathermap.org/data/2.5/onecall?lat=${item.latitude}&lon=${item.longitude}&lang=en& exclude=hourly,daily&units=metric&appid=207c24b8a2b7b48ac770562514f3a809`
      axios.get(URL).then((res: AxiosResponse)=>{
        if(res.status==200){
          list.push({
            name: item.id,
            latitude: item.latitude,
            longitude: item.longitude,
            dt: res.data.current.dt,
            weather: res.data.current.weather,
            temp: res.data.current.temp
          }) 
        }
      })
    }
    this.list = list
  }
  private toggleCity($event:MouseEvent,c: any){
    this.location = city.find(item => item.id==c.name)
    this.getWeather(Number(this.location.latitude), Number(this.location.longitude) )
    this.menu = false
  }
}
