























































import Clipboard from 'clipboard'
import * as hljs from "highlight.js";
import { Vue, Component, Watch } from "vue-property-decorator";
@Component({})
export default class Bar extends Vue {
  private width = 5
  private thumbradius = 5
  private trackradius = 5
  private thumbColor = 'rgba(44,175,254,0.5)'
  private trackColor = 'rgba(0,0,0,1)'
  private get code(){
    return `
  ::-webkit-scrollbar {
    width: ${this.width}px;
  }
  ::-webkit-scrollbar-thumb {
    border-radius: ${this.thumbradius}px;
    background: ${this.thumbColor};
  }
  ::-webkit-scrollbar-track {
    border-radius: ${this.trackradius}px;
    background: ${this.trackColor};
  }`
  };
  @Watch('code')
  onChangeCode(newVal: string, oldVal: string){
    document.querySelectorAll('pre code').forEach((block: any) => {
      block.innerHTML = newVal
      hljs.highlightBlock(block);
    });
  }
  @Watch('width')
  onChangeWidth(newVal: string, oldVal: string){
    document.styleSheets[2].addRule("#scroll::-webkit-scrollbar", `width: ${newVal}px!important;`);
  }
  @Watch('thumbradius')
  onChangeThumbradius(newVal: string, oldVal: string){
    document.styleSheets[2].addRule("#scroll::-webkit-scrollbar-thumb", `border-radius: ${newVal}px!important;background: ${this.thumbColor}!important;`);
  }
  @Watch('trackradius')
  onChangeTrackradius(newVal: string, oldVal: string){
    document.styleSheets[2].addRule("#scroll::-webkit-scrollbar-track", `border-radius: ${newVal}px!important;background: ${this.trackColor}!important;`);
  }
  @Watch('thumbColor')
  onChangeThumbColor(newVal: string, oldVal: string){
    document.styleSheets[2].addRule("#scroll::-webkit-scrollbar-thumb", `border-radius: ${this.thumbradius}px!important;background: ${newVal}!important;`);
  }
  @Watch('trackColor')
  onChangeTrackColor(newVal: string, oldVal: string){
    document.styleSheets[2].addRule("#scroll::-webkit-scrollbar-track", `border-radius: ${this.trackradius}px!important;background: ${newVal}!important;`);
  }
  private mounted() {
    const btn = new Clipboard('.btn')
    hljs.configure({
      useBR: false,
      tabReplace: "  ",
    });
    document.querySelectorAll('pre code').forEach((block: any) => {
      // block.removeChild(document.querySelector('code'))
      block.innerHTML = this.code
      console.log(block)
      hljs.highlightBlock(block);
    });
    // document.querySelectorAll('pre code').forEach((block: any) => {
    //   hljs.highlightBlock(block);
    // });
  }
  private copy(){
    // 
    const temp = document.createElement('input')
    temp.setAttribute('value', this.code)
    document.body.appendChild(temp)
    temp.select()
    document.execCommand('copy')
    document.body.removeChild(temp)
  }
}
