






























import Cities from './heritage_data'
import gsap from 'gsap'
import {Vue, Component, Watch} from 'vue-property-decorator';


@Component({})
export default class Clock extends Vue{
  private cities: any[] = Cities
  private city: any
  private times: string[] = []
  private imgs: string[] = []
  private count: number = 0
  private created(){
    this.city = this.cities.find( item => item.id === this.$route.params.id )
    for( let key in this.city.fp ){
      this.times[Number(key)] = this.city.fp[key].slice(0, 2) + ':' + this.city.fp[key].slice(2)
      this.imgs[Number(key)] = `https://www.sony.net/united/clock/share/img/photo/${this.city.id}/fp_hd/${key}.jpg`
      const img = new Image()
      img.src = this.imgs[Number(key)]
      img.onload = ()=>{ this.count++ }
    }
    this.times.shift()
    this.imgs.shift()
    this.imgs.reverse()
  }
  private mounted(){
    gsap.to('#cover>div', { height: 'auto', duration: 1})
    gsap.to('#cover>img',{ scale: 1.2, duration: 5, repeat: -1, yoyo: true  })
  }
  private roll($event: MouseWheelEvent){
    // 控制滚轮精度
    (document.querySelector('#app') as HTMLElement).scrollTop += $event.deltaY > 0 ? 40 : -40
    const hight = window.innerHeight
    const scroll = (document.querySelector('#app') as HTMLElement).scrollTop
    // compute
    const per = (scroll % hight) / hight
    const index = Math.floor(scroll / hight)
    document.querySelectorAll('.photo')[11 - index].style.opacity = 1 - per < 0.05 ? 0: 1 - per
    $event.preventDefault()
    
  } 
  @Watch('count')
  private hide(newValue: number){
    if(newValue == 12 ){
      
      gsap.to('#cover>div', {display: 'none', height: 0, duration: 1, padding: 0, border: 'none', delay: 1})
      gsap.to('#cover',{ display: 'none', height: 0, duration: 1, delay: 1.7 })
      gsap.to('#times',{display: 'block', duration: 1, delay: 1.7})
      gsap.to('#times>.time>p',{display: 'block', opacity: 1,height: 155, duration: 1, delay: 1.7})
      gsap.to('#photos',{display: 'block', duration: 1, delay: 1.7})
    }
  }
}
