



























import moment from 'moment-timezone'
import city from './heritage_data'
import gsap from 'gsap'
import echarts from 'echarts'
import axios,{AxiosResponse} from 'axios'
import {Vue,Component} from 'vue-property-decorator'
interface Onecall {
  lat?: number;
  lon?: number;
  timezone?: string;
  timezone_offset?: number;
  current?: any;
  hourly?: any[];
  daily?: any[]
}
@Component({
  filters:{
    format(value: number,tz: string,str: string){
      if(!value) return ''
      return moment.tz(value*1000, tz).format(str)
    }
  }
})
export default class PC extends Vue{
  private location: any = city[0]
  private onecall: Onecall = {
    hourly: [],
    daily: []
  }
  private hourLine :number[][] =[]
  private dayLine: number[][]=[]
  private list: any[]=[]
  private hourChart: any
  private dayChart: any
  private bg = ""
  private mounted(){
    this.getWeather(Number(this.location.latitude), Number(this.location.longitude) )
    
  }
  private getDayLine(data: number[][]){
    this.dayChart = echarts.init((document.querySelector('#days-line') as HTMLDivElement))
    const option = {
          grid: {
            bottom: 18,
            right: 40
          },
          legend: {
              data: ['MAX', 'MIN'],
              top: -5
          },
          xAxis: {
              type: 'category',
              boundaryGap: false
          },
          yAxis: {
              type: 'value',
              axisLabel: {
                  formatter: '{value}'
              }
          },
          series: [
              {
                  name: 'MAX',
                  type: 'line',
                  areaStyle: {},
                  data: data[0],
                  markPoint: {
                      data: [
                          {type: 'max', name: 'max'},
                          {type: 'min', name: 'min'}
                      ]
                  },
                  markLine: {
                      data: [
                          {type: 'average', name: '平均值'}
                      ]
                  }
              },
              {
                  name: 'MIN',
                  type: 'line',
                  data: data[1],
                  areaStyle: {},
                  markPoint: {
                      data: [
                          {name: '周最低', value: -2, xAxis: 1, yAxis: -1.5}
                      ]
                  },
                  markLine: {
                      data: [
                          {type: 'average', name: '平均值'},
                          [{
                              symbol: 'none',
                              x: '90%',
                              yAxis: 'max'
                          }, {
                              symbol: 'circle',
                              label: {
                                  position: 'start',
                                  formatter: 'max'
                              },
                              type: 'max',
                              name: 'max'
                          }]
                      ]
                  }
              }
          ]
    };
    this.dayChart.setOption(option)
  }
  private getHourLine(data: number[][]){
    this.hourChart = echarts.init((document.querySelector('#tempLine') as HTMLDivElement))
    const option = {
          grid: {
            bottom: 0,
            right: 0,
            left: 0
          },
          xAxis: {
              type: 'category',
              boundaryGap: false,
              show: false
          },
          yAxis: {
              type: 'value',
              axisLabel: {
                  formatter: '{value}'
              },
              show: false
          },
          series: [
              {
                  name: 'Feels Like',
                  type: 'line',
                  data: data[1],
                  symbolSize: 1,
                  smooth: true,
                  label:{
                    show: true,
                    color: '#fff',
                  },
                  areaStyle: {
                    color: 'white',
                    opacity: 1
                  },
                  itemStyle: {
                    opacity: 1,
                    color: 'white'
                  },
                  lineStyle: {
                    opacity: 0
                  },
              }
          ]
    };
    this.hourChart.setOption(option)
  }
  private getWeather(lat: number, lon: number){
    const URL = `https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lon}&lang=en& exclude=hourly,daily&units=metric&appid=207c24b8a2b7b48ac770562514f3a809`
    axios.get(URL).then((res: AxiosResponse)=>{
      if(res.status==200){
        this.onecall = res.data
        this.hourLine[0] = (this.onecall.hourly || []).map(item => parseInt(item.temp)) 
        this.hourLine[1] = (this.onecall.hourly || []).map(item => parseInt(item.feels_like)) 
        this.dayLine[0] = (this.onecall.daily || []).map(item => parseInt(item.temp.max)) 
        this.dayLine[1] = (this.onecall.daily || []).map(item => parseInt(item.temp.min))
        this.getHourLine(this.hourLine)
       //  this.getDayLine(this.dayLine)
       let time = moment.tz(this.onecall.current.dt*1000, this.onecall.timezone||"").format("HHmm")
       this.getCloseTime(time)
      }
    })
  }
  private getCloseTime(time: string){
    let timeArr = []
    for (const key in this.location.fp) {
      if (this.location.fp.hasOwnProperty(key)) {
        const element = this.location.fp[key];
        timeArr.push([element, key])
      }
    }
    let bg = timeArr.sort().find(item=>{
      return Number(item[0])>=Number(time)
    })
    this.bg = bg ? bg[1] : "01"
  }
} 
