






























import gsap from 'gsap';
import {Vue, Component} from 'vue-property-decorator';
import CursorMd from './CursorMd.md';
@Component({
  components: {
    CursorMd,
  },
})
export default class Cursors extends Vue {
 private frequency: any = { turbulence: 0 } ;
 private mouseLocation: any = { x: 0, y: 0 };
 private mounted() {
   [...document.querySelectorAll('a')].forEach((item) => {
     item.addEventListener('mouseenter', ()  => {
      gsap.to(this.frequency, {duration: 1, turbulence: 10});
      gsap.to(this.frequency, {duration: 1, turbulence: 0});
     });
   });
 }
 private UpdateCursor($event: MouseEvent) {
    gsap.to(this.mouseLocation, {duration: 1, x: $event.clientX});
    gsap.to(this.mouseLocation, {duration: 1, y: $event.clientY});
 }
}
