


















































































































































































import moment from '../../../node_modules/moment';
import gsap from 'gsap';
import _ from '../../../node_modules/lodash';
import { Component, Vue } from 'vue-property-decorator';
import FlipClock from './FlipClock.md';


@Component({
  components: {
    FlipClock,
  },
})
export default class Flip extends Vue {
  private time: any;
  private hPrefri: string = '0';
  private hPresec: string = '0';
  private mPrefri: string = '0';
  private mPresec: string = '0';
  private sPrefri: string = '0';
  private sPresec: string = '0';
  private hNextfri: string = '0';
  private hNextsec: string = '0';
  private mNextfri: string = '0';
  private mNextsec: string = '0';
  private sNextfri: string = '0';
  private sNextsec: string = '0';
  private mounted() {
    this.time = moment().millisecond(0);
    this.initTime();
    setInterval(() => { this.initTime(); }, 1000);
  }
  private initTime() {
    this.time = this.time.add(1, 's');
    const current =  _.cloneDeep(this.time);
    const hpre: string = current.format('HH');
    const mpre: string = current.format('mm');
    const spre: string = current.format('ss');
    this.hPrefri  = hpre.charAt(0);
    this.hPresec  = hpre.charAt(1);
    this.mPrefri = mpre.charAt(0);
    this.mPresec = mpre.charAt(1);
    this.sPrefri  = spre.charAt(0);
    this.sPresec = spre.charAt(1);
    setTimeout(() => {
      const hnext: string = current.add(1, 'h').format('HH');
      const mnext: string = current.add(1, 'm').format('mm');
      const snext: string = current.add(1, 's').format('ss');
      this.hNextfri = hnext.charAt(0);
      this.hNextsec = hnext.charAt(1);
      this.mNextfri  = mnext.charAt(0);
      this.mNextsec  = mnext.charAt(1);
      this.sNextfri  = snext.charAt(0);
      this.sNextsec = snext.charAt(1);
    }, 200);

    if (this.sPresec === '9') {
      gsap.to('#second .first .pre .up', 1, { className : 'up active', duration: 1, onComplete: () => {
        gsap.set('#second .first .pre .up', {className : 'up'});
      }});
      gsap.to('#second .first .next .down', 1, { className : 'down active', duration: 1, onComplete: () => {
         gsap.set('#second .first .next .down', {className : 'down'});
      }});
      gsap.to('#second .first .next', 1 , { className : 'next active', duration: 1, onComplete: () => {
        gsap.set('#second .first .next', {className : 'next'});
      }});
    }
    if (spre === '59') {
      gsap.to('#minute .second .pre .up', 1, { className : 'up active', duration: 1, onComplete: () => {
        gsap.set('#minute .second .pre .up', {className : 'up'});
      }});
      gsap.to('#minute .second .next .down', 1, { className : 'down active', duration: 1, onComplete: () => {
         gsap.set('#minute .second .next .down', {className : 'down'});
      }});
      gsap.to('#minute .second .next', 1 , { className : 'next active', duration: 1, onComplete: () => {
        gsap.set('#minute .second .next', {className : 'next'});
      }});
    }
    if (spre === '59' && this.mPresec === '9') {
      gsap.to('#minute .first .pre .up', 1, { className : 'up active', duration: 1, onComplete: () => {
        gsap.set('#minute .first .pre .up', {className : 'up'});
      }});
      gsap.to('#minute .first .next .down', 1, { className : 'down active', duration: 1, onComplete: () => {
         gsap.set('#minute .first .next .down', {className : 'down'});
      }});
      gsap.to('#minute .first .next', 1 , { className : 'next active', duration: 1, onComplete: () => {
        gsap.set('#minute .first .next', {className : 'next'});
      }});
    }
    if (spre === '59' && mpre === '59') {
      gsap.to('#hour .second .pre .up', 1, { className : 'up active', duration: 1, onComplete: () => {
        gsap.set('#hour .second .pre .up', {className : 'up'});
      }});
      gsap.to('#hour .second .next .down', 1, { className : 'down active', duration: 1, onComplete: () => {
         gsap.set('#hour .second .next .down', {className : 'down'});
      }});
      gsap.to('#hour .second .next', 1 , { className : 'next active', duration: 1, onComplete: () => {
        gsap.set('#hour .second .next', {className : 'next'});
      }});
    }
    if (spre === '59' && mpre === '59' && (hpre === '09' || hpre === '19' || hpre === '23')) {
      gsap.to('#hour .first .pre .up', 1, { className : 'up active', duration: 1, onComplete: () => {
        gsap.set('#hour .first .pre .up', {className : 'up'});
      }});
      gsap.to('#hour .first .next .down', 1, { className : 'down active', duration: 1, onComplete: () => {
         gsap.set('#hour .first .next .down', {className : 'down'});
      }});
      gsap.to('#hour .first .next', 1 , { className : 'next active', duration: 1, onComplete: () => {
        gsap.set('#hour .first .next', {className : 'next'});
      }});
    }
  }
}
