















import gsap from 'gsap'
import textPlugin from 'gsap/TextPlugin'
import {Component,Vue} from 'vue-property-decorator'
gsap.registerPlugin(textPlugin)

@Component({})
export default class Typing extends Vue{
  private texts: string[] =[
    "UI & UX designer",
    "Webdesigner",
    "FullStack developer"
  ]
  private index:number = 0
  private mounted(){
    gsap.to('.cursor', { opacity: 0, duration: 0.5, yoyo: true, repeat: -1 })
  

    const text = gsap.timeline({
      repeat:-1,
      repeatDelay: 1,
    })
    this.texts.forEach((item:string,index: number)=>{
      text.to('.text',{
          duration: 1, 
          text: item,
          ease: "none",
          repeatDelay: 0.5,
          repeat:1,
          yoyo: true
      })
    })
  }
  private pushText(text: string){

  }
  private popText(text: string){

  }
}

