



















































import gsap from 'gsap';
import {Power0} from 'gsap/gsap-core';
import {Vue, Component} from 'vue-property-decorator';
@Component
export default class Waters extends Vue {
  private frequency: any = { turbulence: 0.01 } ;
  private mounted() {
    // console.log(1);
    const t1 = gsap.timeline({repeat: -1, yoyo: true});
    t1.to(this.frequency, {duration: 10, turbulence: 0.05, delay: 0, ease: (Power0 as gsap.Linear).easeInOut});
  }
}
