    // }
  ].reverse();
  private translateX: number = 0
  private scroll($event: MouseWheelEvent){
    const list: HTMLElement | null = document.querySelector('#list')
    const catalog : HTMLElement | null = document.querySelector('#items')
    if ( $event.deltaY > 0 ) {
      this.translateX =  this.translateX == -8300 ? -8300  : (this.translateX - 100)
    } else {
      this.translateX = this.translateX == 0 ? 0 : (this.translateX + 100)
    }
    gsap.to(list,{translateX: this.translateX,duration:0.5})
    gsap.to(catalog,{translateY: this.translateX / 10,duration:0.5})
    $event.preventDefault()
  }
}
