





import * as PIXI from 'pixi.js';
import { Vue, Component } from 'vue-property-decorator';
@Component({})
export default class Maze extends Vue {
  private app: PIXI.Application | undefined;
  private mounted() {
    this.app = new PIXI.Application({
      width: 512,
      height: 512,
      transparent: true,
      resolution: 1,
      antialias: true,
    });

    (document.querySelector('#maze') as HTMLElement).appendChild(this.app.view);
    this.app.loader.add('treasureHunter', './images/treasureHunter2.json').load(this.setup);
  }
  private setup(loader: any, resource: any) {
    const id: PIXI.ITextureDictionary  = loader.resources.treasureHunter.textures;
    const gameScene: PIXI.Container = new PIXI.Container();
    const gameOverScene: PIXI.Container = new PIXI.Container();
    gameOverScene.visible = false;
    (this.app as PIXI.Application).stage.addChild(...[gameScene, gameOverScene]);
    const dungeon = new PIXI.Sprite(id['dungeon.png']);
    const explorer = new PIXI.Sprite(id['explorer.png']);
    explorer.position.set(32, 32);
    // treasure
    const treasure = new PIXI.Sprite(id['treasure.png']);
    treasure.position.set(456, 456);
    const x1 = new PIXI.Sprite(id['x.png']);
    x1.position.set(128, 128);
    const x2 = new PIXI.Sprite(id['x.png']);
    x2.position.set(128, 160);
    const x3 = new PIXI.Sprite(id['x.png']);
    x3.position.set(128, 192);
    const y = new PIXI.Sprite(id['y.png']);
    y.position.set(256, 256);
    gameScene.addChild(dungeon, explorer, x1, x2, x3, y, treasure);
  }
}
