





import * as THREE from 'three'
import {Vue, Component} from 'vue-property-decorator'
@Component({})
export default class Three extends Vue{
  private mounted(){
    console.log(THREE)
    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 0.1, 100)
    //camera.position.y = -250; 
    camera.position.z = 50; 
    //camera.rotation.x = 45 * (Math.PI / 180); 
    const render = new THREE.WebGLRenderer({
      alpha: !0,
      antialias: !0
    })
    render.setPixelRatio(1)
    render.setClearColor(16777215, 0)
    render.setSize(window.innerWidth, window.innerHeight);
    (document.querySelector('#three') as HTMLElement).appendChild(render.domElement)

    const geometry = new THREE.PlaneBufferGeometry(1,1,15,1)
    const material = new THREE.ShaderMaterial({
        vertexShader: "precision highp float;\n#define GLSLIFY 1\n\nuniform float uProgress;\nuniform float uOffset;\nuniform float uVelo;\nuniform float uStrength;\n\n#define M_PI 3.1415926535897932384626433832795\n\nvarying vec2 vUv;\n\nvoid main(){\n  vec3 pos = position;\n  vec4 mvPosition = modelViewMatrix * vec4(pos, 1.);\n\n  float dist = mvPosition.x / uOffset;\n  mvPosition.z += sin(dist * M_PI + M_PI / 2.) * -uStrength * (1. + (uVelo * 1.5));\n\n  vUv = uv;\n  gl_Position = projectionMatrix * mvPosition;\n}",
        fragmentShader: "precision highp float;\n#define GLSLIFY 1\n\nuniform sampler2D uTexture;\n\nuniform float uAlpha;\n\nvarying vec2 vUv;\n\nvoid main() {\n  vec2 uv = vUv;\n\n  vec4 texture = texture2D(uTexture, uv);\n  \n  gl_FragColor = texture * uAlpha;\n}"
    })
    const texture = new THREE.Texture()
    
    texture.generateMipmaps = !1
    texture.minFilter = 1006
    texture.magFilter = 1006
    texture.format = 1022

    
    
    // const loader = new THREE.TextureLoader().load('/01.jpg',function(texture){

    //   const img = new THREE.MeshBasicMaterial({ //CHANGED to MeshBasicMaterial
    //     map:texture
    //   });
    //   const cube = new THREE.Mesh(geometry, img)
    
    //   console.log(1)

    
    // })
    var n = new Image();
                    n.src = '/01.jpg',
                    n.decode().then((function() {
                        texture.image = n,
                        texture.needsUpdate = !0
                        console.log(texture)
                        material.uniforms = {
                          uTime: {
                              value: 0
                          },
                          uTexture: {
                              value: texture
                          },
                          uVelo: {
                              value: 1
                          },
                          uStrength: {
                              value: 19.2
                          },
                          uProgress: {
                              value: 0
                          },
                          uAlpha: {
                              value: 1
                          },
                          uOffset: {
                              value: 40.87620488328095
                          }
                      }
                      const cube = new THREE.Mesh(geometry, material)
    const ob = new THREE.Object3D()
    
      ob.add(cube)
      
      const group = new THREE.Group()
      group.add(ob)


      scene.add(cube)
      render.render( scene, camera );
      var animate = function () {
				requestAnimationFrame( animate );

        cube.position.x+=-0.05;
        //cube.scale.x/=2
        camera.updateProjectionMatrix()
				render.render( scene, camera );
			};
    
			animate();
                    }
                    ))
    
    
    
  }
}
