








































import { Vue, Component} from 'vue-property-decorator';
import {gsap} from 'gsap';
@Component({})
export default class Pile extends Vue {
  private mounted() {
    gsap.to('.level6', { opacity: 1 , duration: 0.5 , ease: 'slow', onComplete: () => {
      gsap.to('.level5', { opacity: 1 , duration: 0.5, ease: 'slow' , onComplete: () => {
        gsap.to('.level4', { opacity: 0.8 , duration: 0.5, ease: 'slow' , onComplete: () => {
          gsap.to('.level3', { opacity: 0.6 , duration: 0.5, ease: 'slow' , onComplete: () => {
            gsap.to('.level2', { opacity: 0.4 , duration: 0.5, ease: 'slow' , onComplete: () => {
              gsap.to('.level1', { opacity: 0.2 , duration: 0.5 , ease: 'slow', onComplete: () => {

              }});
            }});
          }});
        }});
      }});
    }});
  }
  private toggleDown() {
    gsap.to('.img.level5', { top: -16, duration: 1, right: 16 });
    gsap.to('.img.level4', { top: -12, duration: 1, right: 12  });
    gsap.to('.img.level3', { top: -8, duration: 1, right: 8  });
    gsap.to('.img.level2', { top: -4, duration: 1, right: 4  });
  }
  private toggleUp() {
    gsap.to('.img.level5', { top: 0, duration: 1, right: 0  });
    gsap.to('.img.level4', { top: 0, duration: 1, right: 0  });
    gsap.to('.img.level3', { top: 0, duration: 1, right: 0  });
    gsap.to('.img.level2', { top: 0, duration: 1, right: 0 });

  }
}
