
























































































































































import axios from 'axios';
import {Component, Vue} from 'vue-property-decorator';
import gsap from 'gsap';
@Component
export default class Blog extends Vue {
  private List: any[] = [] ;
  private Song: number = 0;
  private img: string = '';
  private audio: HTMLAudioElement = new Audio();
  private played: boolean = false;
  private now: string = '0:00';
  private pics: any[] = [];
  private pic: any = {};
  public toggleplay(): void {
    this.played = !this.played;
    if (this.played) {
      this.audio.play();
    } else {
      this.audio.pause();
    }
  }
  public togglenext(): void {
    this.audio.pause();
    this.played = false;
    if (this.Song === this.List.length - 1 ) {
      this.Song = 0;
    } else {
      this.Song++;
    }
    this.img = this.List[this.Song].album.picUrl;
    this.audio.src = ` https://music.163.com/song/media/outer/url?id=${this.List[this.Song].id}.mp3`;
    this.audio.play();
    this.played = true;
  }
  public togglePic(): void {
    const index = this.pics.indexOf(this.pic);
    if (index === this.pics.length - 1) {
      this.pic = this.pics[0];
    } else {
      this.pic = this.pics[index + 1];
    }
  }
  private created() {
    const url = process.env.NODE_ENV == "development" ? '/api/api/playlist/detail?id=559455159' : 'https://music.163.com//api/playlist/detail?id=559455159'
    this.audio.autoplay = false;
    this.audio.src = '';
    axios.get(url)
      .then((res) => {
        this.List = res.data.result.tracks;
        this.img = this.List[this.Song].album.picUrl;
        this.audio.src = ` https://music.163.com/song/media/outer/url?id=${this.List[this.Song].id}.mp3`;
      });
    this.audio.ontimeupdate = () => {
      this.now = `${new Date(this.audio.currentTime * 1000).getMinutes()}:${new Date(this.audio.currentTime * 1000).getSeconds() > 9 ? new Date(this.audio.currentTime * 1000).getSeconds() : '0' + new Date(this.audio.currentTime * 1000).getSeconds()}`;
      // console.log(this.audio.currentTime / this.audio.duration)
      const bar: HTMLElement| null = document.querySelector('#inner');
      if (bar) {
        bar.style.width = `${100 * (this.audio.currentTime / this.audio.duration)}%`;
      }
    };
    axios.get(`https://www.nationalgeographic.com/photography/photo-of-the-day/_jcr_content/.gallery.${new Date().getFullYear()}-${new Date().getMonth() + 1 > 9 ? new Date().getMonth() + 1 : '0' + (new Date().getMonth() + 1)}.json`)
    .then((res) => {
      this.pics = res.data.items;
      this.pic = res.data.items[0];
    });
    axios.get('https://api.openweathermap.org/data/2.5/onecall?lat=33.441792&lon=-94.037689&exclude=hourly,daily&appid=207c24b8a2b7b48ac770562514f3a809')
    .then((res)=>{
      console.log(res)
    })  
  }
  private _hover($event: MouseEvent) {
    const target = document.querySelectorAll('.art')[0];
    const x = $event.offsetX - target.offsetWidth / 2;
    const y = $event.offsetY - target.offsetHeight / 2;
    const rx = 10 * (x / (target.offsetWidth / 2));
    const ry = 10 * (y / (target.offsetHeight / 2));
    gsap.to(document.querySelectorAll('.art')[0], {rotationX: -ry, rotationY: rx, rotateZ: 0, duration: 0.5, transformPerspective: 1000});
  }
  private _release($event: MouseEvent) {
    gsap.to(document.querySelectorAll('.art')[0], {rotateX: 0, rotateY: 0, rotateZ: 0, duration: 0.5});
  }
}
