import gsap from 'gsap';
import analyze from '../../../node_modules/rgbaster';
import {Component, Vue} from 'vue-property-decorator';

// const Caman = require('caman-dist-only');
@Component
export default class PhotoShop extends Vue {
  public currentX: number = 0;
  public ifDrag: boolean = false;
  public Caman: any = (window as any).Caman;
  public img: any = {};
  public colors: string[] = [];
  public width: number = 0;
  public height: number = 0;
  public C: number = 0;
  public M: number = 0;
  public Y: number = 0;
  public K: number = 0;
  public R: number = 0;
  public G: number = 0;
  public B: number = 0;
  public HEX: string = '';
  public imgData: Uint8ClampedArray = new Uint8ClampedArray();
  // RGB=>HEX
  public RGBToHEX(r: number, g: number, b: number ): string {
    return `#${r < 16 ? 0 : '' }${r.toString(16)}${g < 16 ? 0 : '' }${g.toString(16)}${b < 16 ? 0 : '' }${b.toString(16)}`;
  }
  // paint
  public paintPreset(event: MouseEvent): void {
    const obj = this.img;
    obj.revert(false);
    const filter = event.target ? (event.target as Element).dataset.alias.trim() : '';
    obj[filter]();
    obj.render();
  }
  // 数组元素出现的次数
  // public ItemCount(arr: any[]): any {
  //   const color = {
  //     1 : 1,
  //   };
  //   arr.reduce((obj, current) => {
  //      color[obj] = color[obj] ? color[obj]++ : 1;
  //      return current;
  //   });
  //   return color;
  // }
  // 像素点的颜色
  public PixelColor(event: MouseEvent) {
    const x = event.x;
    const y = event.y;
    const dy = Math.ceil((window.innerHeight * 0.9 - this.height) / 2);
    if (y >= dy && y <= dy + this.height) {
      const index = (y - dy) * window.innerHeight + x;
      this.R = this.imgData[(index - 1) * 4 + 0];
      this.G = this.imgData[(index - 1) * 4 + 1];
      this.B = this.imgData[(index - 1) * 4 + 2];
      this.HEX = this.RGBToHEX(this.R, this.G, this.B);
      const r = this.R / 255;
      const g = this.G / 255;
      const b = this.B / 255;
      const k = Math.min(...[1 - r, 1 - g, 1 - b]);
      this.C = Math.ceil((1 - r - k) / (1 - k) * 100 || 0);
      this.M = Math.ceil((1 - g - k) / (1 - k) * 100 || 0);
      this.Y = Math.ceil((1 - b - k) / (1 - k) * 100 || 0);
      this.K = Math.ceil(k * 100);
    }
  }

  public async InitCanvas(): Promise<any> {
    const canvas: HTMLCanvasElement|null = document.querySelector('canvas');
    if (!canvas) {
      return;
    }
    const img = new Image();
    img.src = require('./a.jpg');
    // img.width = window.innerHeight;
    img.onload = () => {
      const dw = window.innerHeight;
      const dh = window.innerHeight * (img.height / img.width);
      const y = Math.ceil((window.innerHeight * 0.9 - dh) / 2) ;
      this.width = dw;
      this.height = dh;
      canvas.width = dw;
      canvas.height = dh;
      canvas.style.top = `${y}px`;
      const ctx = canvas.getContext('2d');
      if (!ctx) {
        return;
      }
      ctx.drawImage(img, 0, 0, dw, dh);
      this.imgData = ctx.getImageData(0, 0, dw, dh).data;
      this.img = this.Caman('#canvas');
      // this.Caman('#canvas').sharpen(10);
      // this.Caman('#canvas').render();
      // const count = imgData.data.length / 4;
      // const colors = [];
      // for (let i = 0; i < count; i++) {
      //   // colors.push(`#${imgData.data[i * 4 + 0] < 16 ? 0 : ''}${imgData.data[i * 4 + 0].toString(16)}${imgData.data[i * 4 + 1] < 16 ? 0 : ''}${imgData.data[i * 4 + 1].toString(16)}${imgData.data[i * 4 + 2] < 16 ? 0 : ''}${imgData.data[i * 4 + 2].toString(16)}`);
      //   colors.push(`${imgData.data[i * 4 + 0]},${imgData.data[i * 4 + 1]},${imgData.data[i * 4 + 2]}`);
      // }
      // console.log(this.ItemCount(colors));
    };
    // const rst = await analyze(require('./a.jpg'), { ignore: [ 'rgb(255,255,255)', 'rgb(0,0,0)' ] , scale: 0.6 });
    // this.colors.push(rst[0].color);
    // this.colors.push(rst[1].color);
    // this.colors.push(rst[2].color);
    // this.colors.push(rst[3].color);
    // this.colors.push(rst[4].color);
    // console.log(this.colors)
    const test = V.from(require('./a.jpg')).maxColorCount(5).quality(1).getPalette().then((p) => {
      for (const key in p) {
         if (p.hasOwnProperty(key)) {
           const element = p[key];
           this.colors.push(element ? element.hex : '');
         }
       }
    });
  }

  public Drag(event: DragEvent) {
       if (event.screenX > 0) {
          gsap.to((event.target as Element), {
            x: event.screenX - this.currentX,
          });
       }
  }
  public DragEnd(event: DragEvent) {
    // console.log(event.screenX - this.currentX)
    // gsap.to((event.target as Element), {
    //     left: event.screenX - this.currentX,
    //   });
  }
  public mounted() {
    this.InitCanvas();
  }
}
