





import _ from '../../../node_modules/lodash';
import * as PIXI from 'pixi.js';
import { Vue, Component } from 'vue-property-decorator';


interface IPoint extends PIXI.IPoint {
  vx: number;
  vy: number;
  _dir: number;
  _speed: number;
}
interface IEdge {
  xmin: number;
  xmax: number;
  ymin: number;
  ymax: number;
}
interface ITarget {
  x: number;
  y: number;
}
interface IBlob {
  count: number;
}

@Component({})
export default class Pixis extends Vue {
  private app: PIXI.Application | undefined;
  private explorer: PIXI.Sprite | undefined;
  private blobs: PIXI.Sprite[] = [];
  private state: Function | undefined;
  private isX: number = 0;
  private isY: number = 0;
  private message: PIXI.Text = new PIXI.Text('Game Over');
  private blob: IBlob = {
    count: 10,
  };
  private target: ITarget = {
    x: 448,
    y: 256,
  };
  private anime = (delta: number) => {this.gameLoop(delta); };
  private mounted() {

    this.app = new PIXI.Application({
      width: 512,
      height: 512,
      transparent: true,
      resolution: 1,
      antialias: true,
    });

    (<HTMLElement>document.querySelector('#pixi')).appendChild(this.app.view)
    this.app.loader.add('treasureHunter','./images/treasureHunter.json').load(this.setup)

  }
  private setup(loader: any, resource: any){
    const id: PIXI.ITextureDictionary  = loader.resources["treasureHunter"].textures
    const gameScene: PIXI.Container = new PIXI.Container();
    const gameOverScene: PIXI.Container = new PIXI.Container();
    gameOverScene.visible = false;
    (<PIXI.Application>this.app).stage.addChild(...[gameScene, gameOverScene])
    // (this.app as PIXI.Application).stage.addChild(...[gameScene, gameOverScene])
    // message
    this.message.style = new PIXI.TextStyle({
      fontSize: 150,
      fill: "white",
      fontFamily: 'Game Over'
    })
    this.message.position.x = 256 - this.message.width / 2
    this.message.position.y = 256 - this.message.height / 2
    gameOverScene.addChild(this.message)
    // dungeon
    const dungeon = new PIXI.Sprite(id["dungeon.png"])
    // door
    const door = new PIXI.Sprite(id["door.png"])
    door.position.set(64, 0)
    // explorer
    const explorer = new PIXI.Sprite(id["explorer.png"])
    explorer.position.set(64,64)
    // treasure
    const treasure = new PIXI.Sprite(id["treasure.png"])
    treasure.position.set(this.target.x, this.target.y)
    // blob
    this.blobs = this.createBlobs(this.blob.count,1,id)
    // Blood
    const healthBar = new PIXI.Container()
    healthBar.position.x = 320
    healthBar.position.y = 4

    //outer
    const outer = new PIXI.Graphics()
    outer.beginFill(0x000000)
    outer.drawRect(0, 0, 100, 8)
    outer.endFill()

    //inner
    const inner = new PIXI.Graphics()
    inner.beginFill(0xFF3300)
    inner.drawRect(0, 0, 100, 8)
    inner.endFill()

    healthBar.addChild(...[outer, inner])




    gameScene.addChild(dungeon, door, explorer, treasure,...this.blobs, healthBar);
    this.explorer = explorer;
    this.state = this.play;

    (this.app as PIXI.Application).ticker.add(this.anime)
  }
  private attackCheck(explorer: PIXI.Sprite, blob: PIXI.Sprite){
    let disX = explorer.position.x - blob.position.x
    let disY = explorer.position.y - blob.position.y
    if((disX<=  30 && disX >= -20) && (disY<=  24 && disY>=-27)){
      this.message.text = 'Game Over'
      this.state = this.end
    }
  }
  private moveBlobs(blob: PIXI.Sprite, edge: IEdge)
  {
    const dir = (blob.position as IPoint)._dir
    if(dir == 0){
      blob.position.y += -(blob.position as IPoint)._speed
      if(blob.position.y <=  edge.ymin){
        (blob.position as IPoint)._dir = 1
      }
    } else if (dir == 1) {
      blob.position.y += (blob.position as IPoint)._speed
      if(blob.position.y >= edge.ymax){
        (blob.position as IPoint)._dir = 0
      }
    } else if (dir ==2) {
      blob.position.x += -(blob.position as IPoint)._speed
      if(blob.position.x <=  edge.xmin){
        (blob.position as IPoint)._dir = 3
      }
    } else if (dir == 3) {
      blob.position.x += (blob.position as IPoint)._speed
      if(blob.position.x >= edge.xmax){
        (blob.position as IPoint)._dir = 2
      }
    }
    // this.hitCheck((this.explorer as  PIXI.Sprite), blob)
  }
  //TODO: 控制面板
  private win(explorer: PIXI.Sprite){
    const disX = this.target.x - explorer.position.x
    const disY = this.target.y - explorer.position.y
    if((disX<=  30 && disX >= -20) && (disY<=  24 && disY>=-27)){
      this.message.text = 'You Win'
      this.state = this.end
    }
  }
  private createBlobs(count: number, speed: number,textures: PIXI.ITextureDictionary): PIXI.Sprite[]{
    let array: PIXI.Sprite[] = []
    const pre = 64
    const offset = 384 / ((this.blob.count / 2 - 1) <=  0 ? 2 : (this.blob.count / 2 - 1)) - 16
    for(let i=0;i<= count-1;i++){
      const blob = new PIXI.Sprite(textures["blob.png"])
      if (i < count / 2){
        const y = 64
        const x = 64 + offset * i
        blob.position.set(x, y);
      } else {
        const x = 64
        const y = 64 + offset * ( i - count / 2)
        blob.position.set(x, y);
      }

      (blob.position as IPoint)._dir = _.random(0, 3, false);
      (blob.position as IPoint)._speed = _.random(1, 3, true);
      array.push(blob)
    }
    return array
  }
  private ctrlExplorer(keyCode: number){

    // up
    if(keyCode == 38){
      console.log(this.isY)
      if( this.isY == 1) return
      this.isY = -1;
      ((this.explorer as PIXI.Sprite).position as IPoint).vy = -1;
      ((this.explorer as PIXI.Sprite).position as IPoint).vx = 0
    }
    // down
    if(keyCode == 40){
      if( this.isY == -1) return
      this.isY = 1;
      ((this.explorer as PIXI.Sprite).position as IPoint).vy = 1;
      ((this.explorer as PIXI.Sprite).position as IPoint).vx = 0
    }
    // left
    if(keyCode == 37){
      if( this.isX == 1) return
      this.isX = -1;
      ((this.explorer as PIXI.Sprite).position as IPoint).vx = -1;
      ((this.explorer as PIXI.Sprite).position as IPoint).vy = 0
    }
    //right
    if(keyCode == 39){
      if( this.isX == -1) return
      this.isX = 1;
      ((this.explorer as PIXI.Sprite).position as IPoint).vx = 1;
      ((this.explorer as PIXI.Sprite).position as IPoint).vy = 0
    }

  }
  private releaseExplorer(keyCode: number){
    if(keyCode == 38){
      if(this.isY == -1) {
        this.isY = 0 ;
        ((this.explorer as PIXI.Sprite).position as IPoint).vy = 0;
      }
    }
    if(keyCode == 40){
      if(this.isY == 1) {
        this.isY = 0 ;
        ((this.explorer as PIXI.Sprite).position as IPoint).vy = 0;
      }
    }
    if(keyCode == 37){
      if(this.isX == -1){
        this.isX = 0;
        ((this.explorer as PIXI.Sprite).position as IPoint).vx = 0;
      }
    }
    if(keyCode == 39){
      if(this.isX == 1){
        this.isX = 0;
        ((this.explorer as PIXI.Sprite).position as IPoint).vx = 0;
      }
    }
  }
  private play(delta: number){

    (this.app as PIXI.Application).stage.children[0].visible = true;
    (this.app as PIXI.Application).stage.children[1].visible = false;
    // edge
    const edge:IEdge={
      xmin: 32,
      xmax: 460,
      ymin: 10,
      ymax: 450
    }
    let nextX = (this.explorer as PIXI.Sprite).position.x + ((this.explorer as PIXI.Sprite).position as IPoint).vx || 0;
    (this.explorer as PIXI.Sprite).position.x = nextX < edge.xmin ? edge.xmin : (nextX > edge.xmax ? edge.xmax : nextX);
    let nextY = (this.explorer as PIXI.Sprite).position.y + ((this.explorer as PIXI.Sprite).position as IPoint).vy || 0;
    (this.explorer as PIXI.Sprite).position.y = nextY < edge.ymin ? edge.ymin : (nextY > edge.ymax ? edge.ymax : nextY);
    this.blobs.forEach(item=>{
      this.moveBlobs(item, edge)
      this.attackCheck((this.explorer as  PIXI.Sprite), item)
    })
    this.win((this.explorer as PIXI.Sprite))


  }
  private gameLoop(delta: number){
    (this.state as Function)(delta)
  }
  private end(){
    (this.app as PIXI.Application).stage.children[0].visible = false;
    (this.app as PIXI.Application).stage.children[1].visible = true;
  }
  private restart(){
    if(this.state == this.end){
       (<PIXI.Application>this.app).stage.removeChildren();
       (this.app as PIXI.Application).ticker.remove(this.anime);
       this.blobs = []
       this.explorer = undefined
       this.setup((this.app as PIXI.Application).loader, (this.app as PIXI.Application).loader.resources)
       this.state = this.play
    }
  }
  private created(){
    const self = this
    document.onkeydown = ($event: KeyboardEvent)=>{
      const keyCode = $event.keyCode
      this.ctrlExplorer(keyCode)
      if(keyCode == 13){
        self.restart()
      }
      $event.preventDefault()
    }
    document.onkeyup=($event: KeyboardEvent)=>{
      const keyCode = $event.keyCode
      this.releaseExplorer(keyCode)
      $event.preventDefault()
    }
  }
}
;
