// import 'pixi-projection'
import gsap from 'gsap';
import {Vue, Component} from 'vue-property-decorator';
@Component({})
export default class nebulon extends Vue {
  private app: PIXI.Application = new PIXI.Application({
    width: window.innerWidth,
    height: window.innerHeight,
    resolution: 1,
    transparent: true,
    antialias: true,
  });
  private cloud: PIXI.Sprite = new PIXI.Sprite();
  private mounted() {
    (document.querySelector('#nebulon') as HTMLElement).appendChild(this.app.view);
    this.app.loader
      .add('t1.png')
      .add('t2.png')
      .add('t3.png')
      .add('t4.png')
      .add('t5.png')
      .add('t6.png')
      .load(this.setup);

  }
  private setup(loader: any, resource: any) {
    const scene = new PIXI.projection.Container3d();
    scene.position3d.z = 100;
    scene.position3d.y = -100;
    scene.scale3d.set(1, 0.5, 0.1);
    this.app.stage.addChild(scene);
    console.log(resource['t2.png']);
    this.cloud = new PIXI.Sprite(resource['t4.png'].texture);
    this.cloud.position.x = 0;
    this.cloud.position.y = 500;
    scene.addChild(...this.createClouds(resource));

    this.app.ticker.add(() => { this.play(); });
  }
  private play() {

  }
  private createClouds(resource: any) {
    const arr: PIXI.Sprite[] = [];

    for (let i = 0; i <= 0; i++) {
      const cloud = new PIXI.projection.Sprite3d(resource['t4.png'].texture);
      // cloud.convertTo3d()
      cloud.position3d.x = 0 + i % 3  * 450;
      cloud.position3d.y = 200 + i % 10  * 200;
      cloud.position3d.z = -100;
      cloud.scale.x = 1;
      cloud.scale.y = 1;
      cloud.alpha = 1;
      cloud.rotation = 0.2;
      arr.push(cloud);
      console.log(cloud);
    }

    return arr;
  }
}
